import uniStepsNew from "@/components/uni-steps-new/uni-steps.vue";
import {
	getApplyChildOrg
} from '@/api/declare.js'
import store from '@/store'
export default {
	components: {
		"uni-steps-new": uniStepsNew
	},
	data() {
		return {
			id: 0,
			planid: 0,
			applyid: 0,
			options: []

		}

	},
	onLoad(option) {
		if (option.id != undefined) {
			this.id = option.id;
			this.planid = option.planid;
			this.applyid = option.applyid;
			this.getModel();
		}
	},
	methods: {
		getModel() {
			uni.showLoading({
				title: "加载中..."
			})
			getApplyChildOrg({
				token_wx: store.getters.token,
				id: this.id
			}).then(res => {
				var log = res.data.log;
				var options = [];

				log.forEach(item => {
					var type = "通过";
					var color = "#4CD964"
					if (item["type"] == 2) {
						type = "驳回";
						color = "#FF0000"
					} else if (item["type"] == 3) {
						type = "不通过";
						color = "#FF8D1A"
					}
					options.unshift({
						title: item.username + " " + type,
						desc: item.addtime,
						color: color,
						detail: item.opinion,
					})
				});
				this.options = options;
				uni.hideLoading()
			})
		}

	}

}
